import logins from "../logins.module.css";
import FormTitle from "../FormTitle.js";
import Logo from "../Logo.js";
import OrIN from "./OrIN";
import { Link, useNavigate } from "react-router-dom";


import { auth } from "firebase-config";

import {
    createUserWithEmailAndPassword, GoogleAuthProvider,
    signInWithPopup, signInWithEmailAndPassword
} from "firebase/auth";



import { useState } from "react";


function Signin() {



    const navigate = useNavigate();  // useNavigate     

    const [newAccount, setNewAccount] = useState(true); //      
    const [userData, setUserData] = useState(null);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(' ');



    const toggleAccount = () => setNewAccount((prev) => !prev);

    




    // input 
    const onChange = (e) => {
        const {
            target: { name, value },
        } = e;
        if (name === 'email') {
            setEmail(value);
        } else {
            setPassword(value);
        }

    };


    const onGoogleClick = async (e) => {
        const { target: { name } } = e; // const name = e.target.name;
        let provider; // Creates the provider object.

        if (name === 'google') {
            provider = new GoogleAuthProvider();
            provider.addScope('profile');
            provider.addScope('email');

        }
        const user = await signInWithPopup(auth, provider);
        setUserData(user);
    }

    const onSubmit = async (e) => {
        e.preventDefault();
        let data;
        try {
            if (newAccount) (
                data = await createUserWithEmailAndPassword(auth, email, password)
            );
            else (
                data = await signInWithEmailAndPassword(auth, email, password)
            );
            setUserData(data);
            navigate('/');


        } catch (error) {
            switch (error.code) {
                case "auth/user-not-found":
                case "auth/wrong-password":
                    setError("    .");
                    break;
                case "auth/email-already-in-use":
                    setError("   .");
                    break;
                case "auth/weak-password":
                    setError(" 6  .");
                    break;
                case "auth/network-request-failed":
                    setError("   .");
                    break;
                case "auth/invalid-email":
                    setError("  .");
                    break;
                case "auth/internal-error":
                    setError(" .");
                    break;
                default:
                    setError("  .");
                    break;
            }
        }
        console.log('user', userData.uid);
    };




    return (

        <div className={logins.box}>
            <Link to="/" className={logins.logo}>
                <Logo text={"PostureFlow"} />
            </Link>

            <FormTitle text={newAccount ? "" : ""} />
            <form onSubmit={onSubmit}>
                <input
                    name="email"
                    type="text" placeholder=" E-mail " className={logins.input} required
                    value={email}
                    onChange={onChange} />
                <input
                    name="password"
                    value={password}
                    type="password" placeholder=" " className={logins.input} required
                    onChange={onChange} />
                <input className={logins.btn} type="submit" value={newAccount ? "" : ""} />

            </form>
            {error}
            <div className={logins.ForSignUpBox}>
                <span onClick={toggleAccount} className={logins.ForSignUpText}>{newAccount ? "  ?" : "  ?"}</span>
                <span onClick={toggleAccount} className={logins.ForSignUp}>{newAccount ? "" : ""}</span>
            </div>
            <OrIN text={""} />
            <Link to="/" className={logins.txtd}>
                <button name="google" onClick={onGoogleClick} className={logins.googleIN}>  </button>
            </Link>


        </div>
    );
}

export default Signin;